/*===========================================
================ CHUNK: BASE/RTYPOGRAPHY
===========================================*/
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto/Roboto-Regular.ttf");
}
html {
  font-size: min(1rem + 0.1764705882vw, 1.35rem);
  font-family: "Roboto", sans-serif;
  color: #444;
}

/*===========================================
================ CHUNK: BASE/RESET
===========================================*/
* {
  box-sizing: border-box;
  margin: 0;
}

body {
  scroll-behavior: smooth;
  min-height: 100vh;
}

button, input, select, textarea {
  width: 190px;
}

p {
  max-width: 550px;
}

a,
a:active,
a:hover,
a:visited {
  text-decoration: none;
  color: #4d58f0;
}

/*===========================================
================ CHUNK: BASE/LAYOUT
===========================================*/
header {
  min-width: 468px;
  padding: 40px 5%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
header .player {
  width: 130px;
}
header .player input, header .player select {
  width: 100%;
  margin-top: 10px;
}
header .game-controls {
  width: 110px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
header .game-controls button {
  width: 100%;
}
header .game-controls button + button {
  margin-top: 10px;
}

.gameboard {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-template-rows: repeat(3, 100px);
  place-content: center;
}
.gameboard .block:nth-of-type(2) {
  border-right: 1px solid #EEE;
  border-left: 1px solid #EEE;
}
.gameboard .block:nth-of-type(5) {
  border: 1px solid #EEE;
}
.gameboard .block:nth-of-type(8) {
  border-right: 1px solid #EEE;
  border-left: 1px solid #EEE;
}
.gameboard .block:nth-of-type(4) {
  border-top: 1px solid #EEE;
  border-bottom: 1px solid #EEE;
}
.gameboard .block:nth-of-type(7) {
  border-top: 1px solid #EEE;
  border-bottom: 1px solid #EEE;
}

button {
  font-size: 0.8rem;
  text-align: center;
  padding: 5px 0;
}